@value makerBlue, makerBlack, makerOrange, makerLightGray from "../../common/colors.css";
@value mediaQuery from "../../common/size.css";

.preview {
  flex-basis: 50%;
  padding: 10px 15px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media screen and (max-width: mediaQuery) {
  .preview {
    padding: 40px 0;
  }
}
